import { type } from '@testing-library/user-event/dist/type';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Nav, NavItem, NavLink, TabContent, TabPane } from 'reactstrap';

const Category = () => {

    const [activeTab, setActiveTab] = useState('0');
    const [category, setCategory] = useState([]);
    const [cardData, setCardData] = useState([]);

    useEffect(() => {
        getCategory();
    }, []);

    /* 카테고리 리스트 조회 API */
    const getCategory = () => {
        axios.get('/')
            .then((res) => {
                setCategory(res.data);
            })
        // fetch('http://localhost:8080')
        //     .then((res) => {
        //         setCategory(res.json());
        //     })
    }

    /* 게시글(카드) 리스트 조회 API - api 개발 필요 */
    const getCardData = (category) => {
        axios.get('/api/card/${category}')
            .then((res) => {
                setCardData(res.data);
        })
    }



    /*  */
    const CategoryType = [ "전체", "한식", "중식", "양식", "기타" ];

    const CategoryList = [
        {
            DataList: "전체 타입"
        }, {
            DataList: "한식 타입"
        }, {
            DataList: "중식 타입"
        }, {
            DataList: "양식 타입"
        }, {
            DataList: "기타 타입"
        }
    ];



    return (
        // <div>
        //     <ListGroup horizontal>
        //         <ListGroupItem>전체</ListGroupItem>
        //         {CategoryType.map((type,index) => (
        //             <ListGroupItem key={index}>
        //                 {type}
        //             </ListGroupItem>
        //          ))}
        //      </ListGroup>
        // </div>

        /* 카테고리별 카드 분류 */
        <div>
        <Nav tabs>
          {category && category.data.map((type, index) => (
            <NavItem>
                <NavLink className={activeTab == index ? 'active' : ''} onClick={() => setActiveTab(index)}>
                    {type.category_name}
                </NavLink>
            </NavItem>
          ))}
        </Nav>
        {CategoryList.map((list, index) => (
        <TabContent activeTab={activeTab}>
          <TabPane tabId={index}>
                {list.DataList}
            </TabPane>
        </TabContent>
        ))}
      </div>
      
    );
};

export default Category;